<template>
	<div class="recommend" ref="recommend">
		<scroll ref="scroll" class="recommend-content" :data="discList">
			<div>
				<div v-if="recommends.length" class="slider-wrapper" ref="sliderWrapper">
					<slider>
						<div v-for="(item, index) in recommends">
							<a :href="item.linkUrl">
								<img class="needsclick" :src="item.picUrl" @load="loadImage" />
							</a>
						</div>
					</slider>
				</div>
				<div class="recommend-list">
					<h1 class="list-title">热门歌单推荐</h1>
					<ul>
						<li v-for="(item, index) in discList" :key="index" @click="selectItem(item)" class="item">
							<div class="icon">
								<img width="60" height="60" v-lazy="item.imgurl">
							</div>
							<div class="text">
								<h2 class="name" v-html="item.creator.name"></h2>
								<p class="desc" v-html="item.dissname"></p>
							</div>
						</li>
					</ul>
				</div>
				<div class="loading-container" v-show="!discList.length">
					<loading></loading>
				</div>
			</div>
		</scroll>
		<router-view></router-view>
	</div>
</template>

<script type="text/ecmascript-6">
	import Slider from 'base/slider/slider'
	import Loading from 'base/loading/loading'
	import Scroll from 'base/scroll/scroll'
	import {getRecommend, getDiscList} from 'api/recommend'
	import {ERR_OK} from 'api/config'
	import {mapMutations} from 'vuex'
	import axios from 'axios'

	export default {
		data() {
			return {
				recommends: [],
				discList: []
			}
		},
		created() {
			// this._getRecommend()
			// this._getDiscList()
			// 固定数据
			axios('https://www.easy-mock.com/mock/5cc505bebbab9326650c18b5/qq-music/recommends').then((response) => {
				response = response.data
				if (response.errno === ERR_OK) {
					this.recommends = response.data
				}
			})
			axios('https://www.easy-mock.com/mock/5cc505bebbab9326650c18b5/qq-music/disclist').then((response) => {
				response = response.data
				if (response.errno === ERR_OK) {
					this.discList = response.data
				}
			})
		},
		methods: {
			loadImage() {
				if (!this.checkloaded) {
					this.checkloaded = true
					this.$refs.scroll.refresh()
				}
			},
			selectItem(item) {
				console.log('由于请求原因，线上无法获取资源')
				this.$router.push({
					path: `/recommend/${item.dissid}`
				})
				this.setDisc(item)
			},
			_getRecommend() {
				getRecommend().then((res) => {
					if (res.code === ERR_OK) {
						this.recommends = res.data.slider
					}
				})
			},
			_getDiscList() {
				getDiscList().then((res) => {
					if (res.code === ERR_OK) {
						setInterval(() => {
							this.discList = res.data.list
						}, 1000)
					}
				})
			},
			...mapMutations({
				setDisc: 'SET_DISC'
			})
		},
		components: {
			Slider,
			Scroll,
			Loading
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import "~common/stylus/variable"

	.recommend
		position: fixed
		width: 100%
		top: 88px
		bottom: 0
		.recommend-content
			height: 100%
			overflow: hidden
			.slider-wrapper
				position: relative
				width: 100%
				overflow: hidden
			.recommend-list
				.list-title
					height: 65px
					line-height: 65px
					text-align: center
					font-size: $font-size-medium
					color: $color-theme
				.item
					display: flex
					box-sizing: border-box
					align-items: center
					padding: 0 20px 20px 20px
					.icon
						flex: 0 0 60px
						width: 60px
						padding-right: 20px
					.text
						display: flex
						flex-direction: column
						justify-content: center
						flex: 1
						line-height: 20px
						overflow: hidden
						font-size: $font-size-medium
						.name
							margin-bottom: 10px
							color: $color-text
						.desc
							color: $color-text-d
			.loading-container
				position: absolute
				width: 100%
				top: 50%
				transform: translateY(-50%)
</style>


